.skill {
  transition: 0.5s ease-in;
}

.skill .title {
  display: inline-block;
  border-top: 3px solid var(--yello);
  border-bottom: 3px solid var(--yello);
  color: var(--yello);
  padding: 5px 30px;
  margin-top: 42px;
  transition: 0.3s;
  font-size: 23px;
}

.skill .main_container {
  padding: 20px;
  margin-top: 20px;
}
.skill .main_container .main {
  font-family: "JUA";
  font-size: 28px;
}

.skill .main_container .skill_container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.skill .skill_container .skill_item img {
  width: 80px;
  height: 80px;
  display: block;
}

.skill .skill_container .skill_item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.skill .skill_container .skill_item svg {
  padding: 0;
}
.skill .skill_container .divider {
  border: 1px solid #e5e5e5;
  height: 40%;
  margin-right: 25px;
}
.skill .flex {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 655px) {
  .skill .title {
    padding: 5px 20px;
    transition: 0.3s;
    display: inline;
  }
}
